* {
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: black;

  height: 100vh;
}

img {
  left: center;
  top: 25%;
  height: 120px;
  width: 100px;
}

input {
  font-family: "Lucida Console";
  border-radius: 8px;
  width: 15em;
  height: 2em;
  box-shadow: 0 0 4px 3px #940505;
  outline: none;
  margin: 0 0 2em 0;
}

a {
  color: white;
  font-family: "Lucida Console";
  text-decoration: none;
  padding-bottom: 0.2em;
}
